.WeatherForm__Container__2iX5P input {
  font-family: 'Raleway', sans-serif;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 50px;
  outline: none;
  border: none;
  background: #2670AF;
  color: white;
  text-shadow: 0 0 10px #2670AF;
  padding: 0 80px 0 20px;
